.crea-ordine-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  flex-wrap: wrap;
  gap: 20px;
}

.progress-info {
  display: flex;
  align-items: center;
  gap: 20px;
}

.carrello-info {
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: #f5f5f5;
  padding: 8px 16px;
  border-radius: 20px;
}

.prezzo-info {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2px;
}

.prezzo {
  font-weight: 500;
  color: #2e7d32;
}

.prezzo-scontato {
  font-size: 1.1em;
  color: #1976d2;
}

.prezzo-originale {
  font-size: 0.8em;
  color: #666;
  text-decoration: line-through;
}

.sconto-badge {
  font-size: 0.7em;
  background-color: #f57c00;
  color: white;
  padding: 2px 6px;
  border-radius: 10px;
  font-weight: 500;
}

/* Stepper Progress */
.stepper-progress {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  overflow-x: auto;
}

.step-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 80px;
  padding: 10px;
  border-radius: 8px;
  transition: all 0.3s ease;
  opacity: 0.6;
  position: relative;
}

.step-indicator.active {
  opacity: 1;
  background-color: #e3f2fd;
  color: #1976d2;
}

.step-indicator.completed {
  opacity: 1;
  background-color: #e8f5e8;
  color: #2e7d32;
}

.step-indicator.clickable {
  cursor: pointer;
  border: 2px dashed #ff9800;
  opacity: 0.8;
}

.step-indicator.clickable:hover {
  opacity: 1;
  background-color: #fff3e0;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

.bevande-shortcut {
  position: absolute;
  top: -5px;
  right: -5px;
  background-color: #ff9800;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: pulse 2s infinite;
}

.shortcut-icon {
  font-size: 12px !important;
  color: white;
  line-height: 1;
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(255, 152, 0, 0.7); }
  70% { box-shadow: 0 0 0 10px rgba(255, 152, 0, 0); }
  100% { box-shadow: 0 0 0 0 rgba(255, 152, 0, 0); }
}

.step-indicator mat-icon {
  margin-bottom: 8px;
  font-size: 24px;
}

.step-indicator span {
  font-size: 0.8em;
  text-align: center;
}

/* Loading */
.loading-section {
  text-align: center;
  padding: 60px 20px;
}

.loading-spinner {
  font-size: 48px;
  width: 48px;
  height: 48px;
  animation: spin 2s linear infinite;
  color: #1976d2;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Step Content */
.step-content {
  min-height: 400px;
  margin-bottom: 30px;
}

.step-header {
  margin-bottom: 30px;
}

.step-header h3 {
  display: flex;
  align-items: center;
  gap: 12px;
  color: #333;
  margin-bottom: 10px;
}

.required-notice {
  color: #f57c00;
  font-style: italic;
  margin: 0;
}

.no-items {
  text-align: center;
  padding: 60px 20px;
  color: #666;
}

.no-items mat-icon {
  font-size: 48px;
  width: 48px;
  height: 48px;
  margin-bottom: 16px;
}

/* Ingredienti Grid */
.ingredienti-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 24px;
  padding: 20px;
}

/* Nuove Card Ingredienti senza bordi */
.ingrediente-card {
  background: transparent;
  border-radius: 20px;
  padding: 16px;
  transition: all 0.3s ease;
  border: none;
  box-shadow: none;
  position: relative;
  overflow: hidden;
}

.ingrediente-card:hover {
  transform: translateY(-8px);
  background: transparent;
  box-shadow: 0 12px 32px rgba(211, 47, 47, 0.2);
}

.ingrediente-card.unavailable {
  opacity: 0.5;
  background: transparent;
}

.ingrediente-card .card-image {
  width: 100%;
  height: 180px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  overflow: hidden;
  margin-bottom: 16px;
  background: transparent;
  box-shadow: none;
  transition: all 0.3s ease;
}

.ingrediente-card .card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
  border: none;
  outline: none;
}

.ingrediente-card .card-content {
  text-align: center;
  background: transparent;
  border-radius: 16px;
  padding: 16px;
}

.ingrediente-card .ingrediente-nome {
  font-size: 1.2em;
  font-weight: 700;
  color: #333;
  margin: 0 0 8px 0;
  line-height: 1.3;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8);
}

.ingrediente-card .ingrediente-prezzo {
  font-size: 1.3em;
  font-weight: 800;
  color: #d32f2f;
  margin: 0 0 16px 0;
  text-shadow: 0 1px 2px rgba(255, 255, 255, 0.8);
}

.ingrediente-card .quantity-controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  background: linear-gradient(135deg, #d32f2f 0%, #b71c1c 100%);
  border-radius: 30px;
  padding: 12px 20px;
  margin-top: 16px;
  box-shadow: 0 4px 16px rgba(211, 47, 47, 0.3);
}

.ingrediente-card .quantity-controls button {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.ingrediente-card .quantity-controls button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.ingrediente-card .quantity-controls button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.ingrediente-card .quantity-display {
  font-size: 1.2em;
  font-weight: 600;
  color: white;
  min-width: 30px;
  text-align: center;
}

.ingrediente-card .unavailable-message {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  color: #666;
  font-style: italic;
  margin-top: 16px;
  background: transparent;
  border-radius: 20px;
  padding: 8px 16px;
}

.ingrediente-card .unavailable-message mat-icon {
  font-size: 20px;
  width: 20px;
  height: 20px;
  color: #999;
}

/* CSS Material Override per coerenza tema rosso */
::ng-deep .ingrediente-card .mat-mdc-icon-button {
  --mdc-icon-button-state-layer-size: 36px;
  --mdc-icon-button-icon-size: 20px;
}

::ng-deep .ingrediente-card .mat-mdc-icon-button .mat-mdc-button-touch-target {
  width: 36px;
  height: 36px;
}

/* Riepilogo ordine con separazione piatti/bevande */
.ordine-section {
  margin-bottom: 30px;
  padding: 20px;
  border-radius: 12px;
  border: 2px solid;
}

.piatti-section {
  border-color: #4caf50;
  background-color: #f1f8e9;
}

.bevande-section {
  border-color: #2196f3;
  background-color: #e3f2fd;
}

.ordine-info {
  margin-bottom: 15px;
}

.info-badge {
  display: inline-block;
  background-color: rgba(0,0,0,0.1);
  padding: 4px 12px;
  border-radius: 16px;
  font-size: 0.85em;
  font-weight: 500;
}

.subtotale {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(0,0,0,0.1);
  text-align: right;
}

.totale-generale {
  margin-top: 30px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
}

.totale-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2em;
  margin-bottom: 10px;
}

.ordini-info {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9em;
  color: #666;
  background-color: #fff3e0;
  padding: 10px;
  border-radius: 6px;
  border-left: 4px solid #ff9800;
}
.riepilogo-section {
  margin-bottom: 40px;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.riepilogo-section h4 {
  color: #333;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.carrello-vuoto {
  text-align: center;
  padding: 40px 20px;
  color: #666;
}

.carrello-vuoto mat-icon {
  font-size: 36px;
  width: 36px;
  height: 36px;
  margin-bottom: 12px;
}

.carrello-items {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.carrello-item {
  display: grid;
  grid-template-columns: 2fr 1fr 80px 100px;
  gap: 16px;
  padding: 12px;
  background-color: white;
  border-radius: 4px;
  align-items: center;
}

.carrello-item .categoria {
  color: #666;
  font-style: italic;
}

.carrello-item .quantita {
  text-align: center;
  font-weight: 500;
}

.carrello-item .prezzo {
  text-align: right;
  font-weight: 500;
  color: #2e7d32;
}

.totale {
  margin-top: 16px;
  padding: 16px;
  background-color: #e8f5e8;
  border-radius: 4px;
  text-align: right;
  font-size: 1.2em;
  color: #2e7d32;
}

.sconto-info {
  margin-bottom: 8px;
  font-size: 0.9em;
}

.sconto-info .prezzo-originale {
  color: #666;
  text-decoration: line-through;
}

.sconto-info .sconto-applicato {
  color: #f57c00;
  font-weight: 500;
}

/* Tavoli */
.tavoli-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 16px;
}

.tavolo-card {
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.tavolo-card:hover {
  box-shadow: 0 4px 8px rgba(0,0,0,0.15);
}

.tavolo-card.selected {
  border-color: #1976d2;
  background-color: #e3f2fd;
}

.tavolo-card mat-card-content {
  text-align: center;
  padding: 20px;
}

.tavolo-card mat-icon {
  font-size: 32px;
  width: 32px;
  height: 32px;
  margin-bottom: 8px;
  color: #1976d2;
}

.no-tavoli {
  text-align: center;
  padding: 40px 20px;
  color: #f57c00;
}

.no-tavoli mat-icon {
  font-size: 36px;
  width: 36px;
  height: 36px;
  margin-bottom: 12px;
}

/* Note Field */
.note-field {
  width: 100%;
}

/* Actions */
.actions-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-top: 1px solid #eee;
}

.actions-right {
  display: flex;
  gap: 12px;
  align-items: center;
}

/* Responsive */
@media (max-width: 768px) {
  .crea-ordine-container {
    padding: 10px;
  }
  
  .header-section {
    flex-direction: column;
    align-items: stretch;
    text-align: center;
  }
  
  .stepper-progress {
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .step-indicator {
    min-width: 60px;
  }
  
  .ingredienti-grid {
    grid-template-columns: 1fr;
  }
  
  .carrello-item {
    grid-template-columns: 1fr;
    text-align: center;
    gap: 8px;
  }
  
  .tavoli-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .actions-section {
    flex-direction: column;
    gap: 16px;
  }
  
  .actions-right {
    width: 100%;
    justify-content: center;
  }
}
